.card{
	position: relative;
	display: block;
	background-color: white;
	box-shadow: 0px 3px rgba(185, 183, 183, 0.25);
	margin-bottom: 1px;
	min-height: 58px;
	padding: 12px;
	border-radius: 9px;
	cursor: pointer;

	&:not(:first-of-type){
		margin-top: 8px;
	}
	>.button-remove {
		display: none;
		position: absolute;
		right: 6px;
		top: 6px;
	}

	&:hover{
		>.button-remove {
			display: flex;
		}
	}
}